.task-container {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(135deg, #667eea, #764ba2);
    font-family: "Segoe UI", sans-serif;
  }
  
  .task-card {
    width: 420px;
    background: #fff;
    padding: 30px;
    border-radius: 16px;
    box-shadow: 0 25px 50px rgba(0, 0, 0, 0.15);
  }
  
  .task-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  input {
    width: 100%;
    padding: 10px;
    margin: 8px 0;
    border-radius: 8px;
    border: 1px solid #ccc;
  }
  
  .add-btn {
    width: 100%;
    margin-top: 10px;
    padding: 10px;
    background: #4f46e5;
    color: #fff;
    border: none;
    border-radius: 8px;
    cursor: pointer;
  }
  
  .logout-btn {
    background: #ef4444;
    color: #fff;
    border: none;
    padding: 6px 12px;
    border-radius: 6px;
    cursor: pointer;
  }
  
  .task-item {
    background: #f9fafb;
    padding: 12px;
    border-radius: 10px;
    margin-bottom: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .task-item.done {
    opacity: 0.7;
  }
  
  .status {
    font-size: 12px;
    font-weight: bold;
  }
  
  .status.pending {
    color: orange;
  }
  
  .status.completed {
    color: green;
  }
  
  .task-actions button {
    margin-left: 6px;
    padding: 6px 10px;
    border-radius: 6px;
    border: none;
    cursor: pointer;
  }
  
  .toggle-btn {
    background: #22c55e;
    color: white;
  }
  
  .delete-btn {
    background: #ef4444;
    color: white;
  }
  